const validateField = (identifier, value) => {
    let regex;
    let errorMessage;

    const errorMessages = {
        tags: "  ",
        english: "  ",
        russian: "  "
    };

    switch(identifier) {
        case 'tags':
        case 'english':
            regex = /^[A-Z]+$/i;
            errorMessage = errorMessages.english;
            break;
        case 'russian':
            regex = /^[-]+$/i;
            errorMessage = errorMessages.russian;
            break;
        default:
            return '';
    }

    if (!regex.test(value)) {
        return errorMessage;
    }
    return '';
};

export default validateField;